@font-face {
	font-family: "swiper-icons";
	src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
	font-weight: 400;
	font-style: normal;
}
:root {
	--swiper-theme-color: #007aff;
}

.swiper {
	margin-left: auto;
	margin-right: auto;
	position: relative;
	overflow: hidden;
	list-style: none;
	padding: 0;
	/* Fix of Webkit flickering */
	z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
	flex-direction: column;
}

.swiper-wrapper {
	position: relative;
	width: 100%;
	height: 100%;
	z-index: 1;
	display: flex;
	transition-property: transform;
	box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
	transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
	touch-action: pan-y;
}
.swiper-pointer-events.swiper-vertical {
	touch-action: pan-x;
}

.swiper-slide {
	flex-shrink: 0;
	width: 100%;
	height: 100%;
	position: relative;
	transition-property: transform;
}

.swiper-slide-invisible-blank {
	visibility: hidden;
}

/* Auto Height */
.swiper-autoheight,
.swiper-autoheight .swiper-slide {
	height: auto;
}
.swiper-autoheight .swiper-wrapper {
	align-items: flex-start;
	transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
	transform: translateZ(0);
	backface-visibility: hidden;
}

/* 3D Effects */
.swiper-3d, .swiper-3d.swiper-css-mode .swiper-wrapper {
	perspective: 1200px;
}
.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
	transform-style: preserve-3d;
}
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
	z-index: 10;
}
.swiper-3d .swiper-slide-shadow {
	background: rgba(0, 0, 0, 0.15);
}
.swiper-3d .swiper-slide-shadow-left {
	background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-right {
	background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-top {
	background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-bottom {
	background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-css-mode > .swiper-wrapper {
	overflow: auto;
	scrollbar-width: none;
	/* For Firefox */
	-ms-overflow-style: none;
	/* For Internet Explorer and Edge */
}
.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
	display: none;
}
.swiper-css-mode > .swiper-wrapper > .swiper-slide {
	scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
	scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
	scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
	content: "";
	flex-shrink: 0;
	order: 9999;
}
.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
	margin-inline-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
	height: 100%;
	width: var(--swiper-centered-offset-after);
}
.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
	margin-block-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-vertical > .swiper-wrapper::before {
	width: 100%;
	height: var(--swiper-centered-offset-after);
}
.swiper-centered > .swiper-wrapper > .swiper-slide {
	scroll-snap-align: center center;
}

html {
	font-size: 10px;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

body {
	margin: 0;
	min-width: 320px;
	background: white;
	color: #27251f;
	font: 400 1.4rem/1.28 "Pan Display";
	font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
}

a {
	text-decoration: none;
	color: inherit;
	transition: all 0.3s ease;
}

a:hover {
	text-decoration: underline;
}

input {
	outline: none;
}

button,
input[type=search],
input[type=button],
input[type=reset],
input[type=submit] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: all 0.3s ease;
}

button::-moz-focus-inner,
input[type=reset]::-moz-focus-inner,
input[type=button]::-moz-focus-inner,
input[type=submit]::-moz-focus-inner {
	border: none;
	padding: 0;
}

.wrapper {
	min-height: 100vh;
	min-height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
}

#main {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

p {
	margin-top: 0;
}

h1,
h2,
h3,
h4,
h5 {
	margin-top: 0;
}

.container {
	max-width: 136.5rem;
	margin: auto;
	padding-left: 2rem;
	padding-right: 2rem;
}
@media (max-width: 767px) {
	.container {
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}
}

.sr-only {
	position: absolute !important;
	width: 1px !important;
	height: 1px !important;
	padding: 0 !important;
	margin: -1px !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	white-space: nowrap !important;
	border: 0 !important;
}

.btn-feedback {
	position: fixed;
	top: 34.4%;
	right: -4.6rem;
	padding: 1.2rem 2rem;
	font-weight: 400;
	font-size: 1.4rem;
	line-height: 1.21;
	letter-spacing: 0.155em;
	text-transform: uppercase;
	transform: rotate(90deg);
	background: white;
	border: 0.1rem solid #b4b4b4;
	border-radius: 0 0 0.3rem 0.3rem;
	z-index: 10;
}
@media (max-width: 767px) {
	.btn-feedback {
		top: 45.9%;
		padding: 2.3rem 0.9rem 0.7rem 0.9rem;
	}
}
.btn-feedback:hover {
	background: #f2f2f2;
	text-decoration: none;
}

.swiper .swiper-pagination {
	position: absolute;
	bottom: 1.5rem;
	left: 50%;
	display: flex;
	transform: translateX(-50%);
	z-index: 2;
}
.swiper .swiper-pagination .swiper-pagination-bullet {
	position: relative;
	display: block;
	width: 1rem;
	height: 1rem;
	cursor: pointer;
}
.swiper .swiper-pagination .swiper-pagination-bullet:not(:last-child) {
	margin-right: 0.2rem;
}
.swiper .swiper-pagination .swiper-pagination-bullet::before {
	content: "";
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 0.6rem;
	height: 0.6rem;
	border-radius: 50%;
	background: #e1e1e2;
}
.swiper .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active::before {
	background: #e6a5b2;
}
.swiper .swiper-navigation .swiper-button-prev,
.swiper .swiper-navigation .swiper-button-next {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	width: 2rem;
	height: 2rem;
	background: transparent;
	border: none;
	z-index: 2;
	background-size: 0.8rem;
	background-repeat: no-repeat;
}
.swiper .swiper-navigation .swiper-button-prev.swiper-button-disabled,
.swiper .swiper-navigation .swiper-button-next.swiper-button-disabled {
	opacity: 0.6;
	pointer-events: none;
}
.swiper .swiper-navigation .swiper-button-prev {
	left: 0;
	background-image: url("data:image/svg+xml,%3Csvg width='9' height='17' viewBox='0 0 9 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.48975 0.50174L0.685242 8.44088L8.48975 16.38' stroke='%2327251F' stroke-width='0.85357'/%3E%3C/svg%3E%0A");
	background-position: center left;
}
.swiper .swiper-navigation .swiper-button-next {
	right: 0;
	background-image: url("data:image/svg+xml,%3Csvg width='9' height='17' viewBox='0 0 9 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.510193 0.50174L8.3147 8.44088L0.510193 16.38' stroke='%2327251F' stroke-width='0.85357'/%3E%3C/svg%3E%0A");
	background-position: center right;
}

/* header */
#header {
	padding-top: 1.4rem;
	padding-bottom: 1.1rem;
	border-bottom: 0.5rem solid #ffcad4;
}
@media (max-width: 767px) {
	#header {
		padding-top: 1.5rem;
		padding-bottom: 1.4rem;
	}
}
#header .row {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
#header .row .col:first-child {
	display: flex;
	align-items: center;
}
@media (min-width: 768px) {
	#header .row .col:first-child {
		display: none;
	}
}
@media (min-width: 768px) {
	#header .row .col:last-child {
		flex: auto;
	}
}
#header a:hover {
	opacity: 0.65;
}
#header .logo {
	display: flex;
	width: 100%;
	max-width: 14.3rem;
}
@media (max-width: 767px) {
	#header .logo {
		max-width: 12.2rem;
		transform: translate(0.5rem, -0.5rem);
	}
}
#header .logo img {
	width: 100%;
	height: auto;
}
#header .search-holder {
	position: relative;
	margin-right: 2rem;
	max-width: 24.1rem;
	width: 100%;
}
@media (max-width: 767px) {
	#header .search-holder {
		display: none;
	}
}
#header .search-holder .btn-search {
	position: absolute;
	top: 50%;
	left: 0.5rem;
	transform: translateY(-50%);
	display: flex;
	padding: 0;
	background: transparent;
	border: none;
}
#header .search-holder .form-control {
	width: 100%;
	padding: 1.2rem 1.2rem 1.2rem 4rem;
	font-size: 1.2rem;
	border: none;
	border: 1px solid #f5f5f5;
}
#header .search-holder .form-control::placeholder {
	color: #b4b4b4;
	text-transform: uppercase;
	letter-spacing: 0.155em;
}
#header .search-holder .form-control:focus {
	border-color: #e8e8e8;
}
#header .header-nav {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: flex-end;
}
#header .nav-menu {
	display: flex;
	align-items: center;
	margin: 0;
	padding: 0;
	list-style: none;
}
@media (min-width: 768px) {
	#header .nav-menu .menu-item:first-child {
		display: none;
	}
}
@media (max-width: 767px) {
	#header .nav-menu .menu-item:nth-child(2), #header .nav-menu .menu-item:nth-child(3) {
		display: none;
	}
}
#header .nav-menu .menu-item:not(:last-child) {
	margin-right: 2rem;
}
@media (max-width: 767px) {
	#header .nav-menu .menu-item:not(:last-child) {
		margin-right: 2.2rem;
	}
}
#header .nav-menu .menu-link {
	display: flex;
}
#header .open-menu {
	position: relative;
	width: 2.2rem;
	height: 2.2rem;
	margin-right: 1.6rem;
	display: block;
	font-size: 0;
	line-height: 0;
}
@media (min-width: 992px) {
	#header .open-menu {
		display: none;
	}
}
#header .open-menu span {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	background: #27251f;
	border-radius: 0.2rem;
	height: 0.2rem;
	transition: all 0.3s ease;
}
#header .open-menu span:nth-child(1) {
	margin-top: -0.8rem;
}
#header .open-menu span:nth-child(2) {
	margin-top: -0.2rem;
}
#header .open-menu span:nth-child(3) {
	margin-top: 0.4rem;
}
.menu-opened #header .open-menu span:nth-child(1) {
	margin-top: 0;
	transform: rotate(45deg);
}
.menu-opened #header .open-menu span:nth-child(2) {
	opacity: 0;
	transform: translateX(50%);
}
.menu-opened #header .open-menu span:nth-child(3) {
	margin-top: 0;
	transform: rotate(-45deg);
}

.btn {
	display: inline-flex;
	justify-content: center;
	padding: 1.5rem;
	font-weight: 400;
	font-size: 1.2rem;
	line-height: 1.23;
